// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: () => ({}),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '0_message': { type: 'message', id: '0_message' },
        },
        outlets: {
            '0': { type: 'message', id: '0' },
        },
        isPullingSignal: true,
    }),
    configureMessageToSignalConnection: (inletId) => {
        if (inletId === '0') {
            return { reroutedMessageInletId: '0_message' };
        }
        return undefined;
    },
};
// ------------------------------- node implementation ------------------------------ //
const nodeImplementation = {
    flags: {
        alphaName: 'snapshot_t',
    },
    state: ({ ns }) => Class(ns.State, [
        Var('Float', 'currentValue', 0)
    ]),
    dsp: ({ ins, state }) => ast `
        ${state}.currentValue = ${ins.$0}
    `,
    messageReceivers: ({ state, snds }) => ({
        '0_message': AnonFunc([Var('Message', 'm')], 'void') `
            if (msg_isBang(m)) {
                ${snds.$0}(msg_floats([${state}.currentValue]))
                return 
            }
        `,
    }),
    dependencies: [
        bangUtils,
    ]
};

export { builder, nodeImplementation };
