// TODO: proper support for $ args
// TODO: simple number - shortcut for float
// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: (pdNode) => ({
        value: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {
            '0': { type: 'message', id: '0' },
            '1': { type: 'message', id: '1' },
        },
        outlets: {
            '0': { type: 'message', id: '0' },
        },
        isPushingMessages: true,
    }),
};
// ------------------------------- node implementation ------------------------------ //
const nodeImplementation = {
    state: ({ node: { args }, ns }) => Class(ns.State, [
        Var('string', 'value', `"${args.value}"`)
    ]),
    messageReceivers: ({ snds, state, }) => ({
        '0': AnonFunc([Var('Message', 'm')]) `
            if (msg_isBang(m)) {
                ${snds.$0}(msg_strings([${state}.value]))
                return
    
            } else if (msg_isMatching(m, [MSG_STRING_TOKEN])) {
                ${state}.value = msg_readStringToken(m, 0)
                ${snds.$0}(msg_strings([${state}.value]))
                return
    
            }
        `,
        '1': AnonFunc([Var('Message', 'm')]) `
            if (msg_isMatching(m, [MSG_STRING_TOKEN])) {
                ${state}.value = msg_readStringToken(m, 0)
                return 
            }
        `,
    }),
    dependencies: [
        bangUtils,
        messageBuses,
    ]
};

export { builder, nodeImplementation };
