// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: () => ({}),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '0_message': { type: 'message', id: '0_message' },
            '1': { type: 'signal', id: '1' },
        },
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    }),
    configureMessageToSignalConnection: (inletId) => {
        if (inletId === '0') {
            return { reroutedMessageInletId: '0_message' };
        }
        return undefined;
    },
};
// ------------------------------- node implementation ------------------------------ //
const nodeImplementation = {
    flags: {
        alphaName: 'samphold_t',
    },
    state: ({ ns }) => Class(ns.State, [
        Var('Float', 'signalMemory', 0),
        Var('Float', 'controlMemory', 0),
    ]),
    dsp: ({ ins, outs, state }) => ast `
        ${state}.signalMemory = ${outs.$0} = ${ins.$1} < ${state}.controlMemory ? ${ins.$0}: ${state}.signalMemory
        ${state}.controlMemory = ${ins.$1}
    `,
    messageReceivers: ({ state }) => ({
        '0_message': AnonFunc([Var('Message', 'm')], 'void') `
            if (
                msg_isMatching(m, [MSG_STRING_TOKEN, MSG_FLOAT_TOKEN])
                && msg_readStringToken(m, 0) === 'set'
            ) {
                ${state}.signalMemory = msg_readFloatToken(m, 1)
                return
    
            } else if (
                msg_isMatching(m, [MSG_STRING_TOKEN, MSG_FLOAT_TOKEN])
                && msg_readStringToken(m, 0) === 'reset'
            ) {
                ${state}.controlMemory = msg_readFloatToken(m, 1)
                return
    
            } else if (
                msg_isMatching(m, [MSG_STRING_TOKEN])
                && msg_readStringToken(m, 0) === 'reset'
            ) {
                ${state}.controlMemory = 1e20
                return
            }
        `,
    }),
};

export { builder, nodeImplementation };
